import React, { useState, useEffect } from 'react';
import { Link } from 'react-scroll';
import { SlSocialGithub, SlSocialLinkedin, SlSocialInstagram, SlHome, SlUser, SlEnvolope, SlGraduation } from 'react-icons/sl'; // Correg el nombre del cono "SlEnvolope" a "SlEnvelope"
import Logo from '../../assets/images/logo.svg';

import "./navbar.css";

const Navbar = () => {
    const [isMenuOpen, setMenuOpen] = useState(false);

    const toggleMenu = () => {
        setMenuOpen(!isMenuOpen);
    };

    const styleToggle = isMenuOpen ? "menu-toggle open" : "menu-toggle";
    const styleNavItems = isMenuOpen ? "nav-items open" : "nav-items";
    
    useEffect(() => {
        // Funcin para manejar el cambio de tamao de la ventana
        const handleResize = () => {
            if (window.innerWidth > 768) {
                setMenuOpen(false); // Cierra el men si la pantalla es ms grande que 768px
            }
        };

        // Agregar el evento de cambio de tamao de la ventana
        window.addEventListener('resize', handleResize);

        // Limpieza del evento al desmontar el componente
        return () => {
            window.removeEventListener('resize', handleResize);
        };
    }, []);

    return (
        <nav>
            <div className='nav-container'>
                <a className='nav-logo' href='#home'>
                    <img src={Logo} alt='logo' />
                </a>
                <ul className={styleNavItems}>
                    <li>
                        <Link
                            to='home' spy={true} smooth={true} offset={-60} duration={500} onClick={toggleMenu}
                        >
                            <SlHome className='nav-items-icon' size={24} />
                            Home
                        </Link>
                    </li>
                    <li>
                        <Link
                            to='about-me' spy={true} smooth={true} offset={-10} duration={500} onClick={toggleMenu}
                        >
                            <SlUser className='nav-items-icon' size={24} />
                            Sobre m
                        </Link>
                    </li>
                    <li>
                        <Link
                            to='experience' spy={true} smooth={true} offset={-10} duration={500} onClick={toggleMenu}
                        >
                            <SlUser className='nav-items-icon' size={24} />
                            Educacin
                        </Link>
                    </li>
                    <li>
                        
                        <Link
                            to='projects' spy={true} smooth={true} offset={-10} duration={500} onClick={toggleMenu}
                        >
                            <SlGraduation className='nav-items-icon' size={24} />
                            Proyectos
                        </Link>
                    </li>
                    <li>
                        <Link
                            to='contact' spy={true} smooth={true} offset={-10} duration={500} onClick={toggleMenu}
                        >
                            <SlEnvolope className='nav-items-icon' size={24} />
                            Contacto
                        </Link>
                    </li>
                </ul>
                <ul className='nav-social-icons'>
                    <li>
                        <a target='_blank' rel='noreferrer' title='LinkedIn' href='https://www.linkedin.com/'><SlSocialLinkedin /></a>
                    </li>
                    <li>
                        <a target='_blank' rel='noreferrer' title='GitHub' href='https://www.github.com/'><SlSocialGithub /></a>
                    </li>
                    <li>
                        <a target='_blank' rel='noreferrer' title='Instagram' href='https://www.instagram.com/'><SlSocialInstagram /></a>
                    </li>
                </ul>
                <div className={styleToggle} onClick={toggleMenu}>
                    <span />
                </div>
            </div>
        </nav>
    );
}

export default Navbar;
