import React from 'react';
import "./experience.css";
import Data from './Data';
import { VerticalTimeline, VerticalTimelineElement } from 'react-vertical-timeline-component';
import "react-vertical-timeline-component/style.min.css";
import { SlGraduation } from 'react-icons/sl'; 

const Experience = () => {
    return (
        <section id='experience'>
            <div className='section-experience'>
                <div className='section-header'>
                    <h2 className='header-title'>Educacin</h2>
                    <p className='header-paragraph'>Resumen de mi trayectoria y experiencias de vida.</p>
                </div>
                <VerticalTimeline>
                    {Data.map(element => {
                        return (
                            <VerticalTimelineElement
                                key={element.key}
                                date={element.date}
                                dateClassName="experience-date"
                                iconStyle={{backgroundColor: "#d0d9ff"}}
                                icon={<SlGraduation style={{color:"#26367a"}}/>}
                            >
                            <h3 className='experience-title'>{element.title}</h3>
                            <p className='experience-desc'>{element.desc}</p>
                            </VerticalTimelineElement>
                        )
                    })}
                </VerticalTimeline>
            </div>
        </section>
    );
}

export default Experience;
